angular
  .module('app')
  .service('ScreenConfigService', function ($http, AppConfig) {
    var svc = this;

    svc.screenCode = '123ABC';
    svc.screenConfig = null;

    const options = {
      headers: {
        'PlanniScreenCode': svc.screenCode
      }
    };

    svc.getScreenConfig = function (callback) {
      if(svc.screenConfig) return callback(null, svc.screenConfig);

      $http.get(AppConfig.apiUrl + 'screen', options).then(function onSuccess(response) {
        svc.screenConfig = response.data;
        console.log(svc.screenConfig);
        callback(null, svc.screenConfig);
      }, function onError(response) {
        callback(response);
      });
    }
  });

angular
  .module('app')
  .config(routesConfig);

/** @ngInject */
function routesConfig($stateProvider, $urlRouterProvider, $locationProvider) {
  // $locationProvider.html5Mode(true).hashPrefix('!');
  $urlRouterProvider.otherwise('/weather');

  $stateProvider
    .state('setup', {
      url: '/setup',
      templateUrl: 'app/setup/setup.html',
      controller: 'SetupController as setup'
    })
    .state('app', {
      url: '/',
      template: '<ui-view/>',
      redirectTo: 'app.weather',
      controller: 'MainController as main'
    })
    .state('app.weather', {
      url: 'weather',
      templateUrl: 'app/weather/weather.html',
      controller: 'WeatherController as weather'
    })
    .state('app.transport', {
      url: 'transport',
      templateUrl: 'app/transport/transport.html',
      controller: 'TransportController as transport'
    })
    .state('app.messages', {
      url: 'messages',
      templateUrl: 'app/messages/messages.html',
      controller: 'MessagesController as messages'
    });
}

angular
  .module('app')
  .controller('MainController', MainController);

/** @ngInject */
function MainController($timeout, $state, $log, $rootScope) {

  // Variables
  currentSlideId = 0;
  slides = [
    {
      name: 'weather',
      duration: 5000
    },
    {
      name: 'transport',
      duration: 5000
    },
    {
      name: 'messages',
      duration: 5000
    }
  ];

  var nextSlide = function () {
    currentSlideId = (currentSlideId + 1 === slides.length ? 0 : currentSlideId + 1);
    $state.go("app." + slides[currentSlideId].name);
    $timeout(nextSlide, slides[currentSlideId].duration);
  }

  function _init() {
    $log.debug('MainController loaded!');

    // nextSlide();
  }
  _init();
}

